.nav{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    z-index: 1;
    display: flex;
    justify-content: space-between;

    transition-timing-function: ease-in;
    transition: all 0.7s;
}
.nav__black{
    background-color: #111;
}
/* Object-fit contain mantiene la calidad sin importar el tamaño */
.nav__logo{
    width: 80px;
    left: 20px;
    position: fixed;
    object-fit: contain;
}
.nav__avatar{
    width: 30px;
    right: 20px;
    position: fixed;
    object-fit: contain;
}